.container_title{
  width: 255px;
  margin-left: auto;
  margin-right: auto;
}

.list_item {
  text-align: center;
  margin-bottom: 20px;
  display: flex;
  text-decoration: none;
  flex-flow: wrap;
  max-width: 960px;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
  transition: border 350ms cubic-bezier(0.4, 0, 0.2, 1);
}

  
.box_description {
  display: flex;
  color: black;
  text-align: left;
  padding-left: 5px;
  padding-right: 5px;
}

.item_price{
  margin-left: auto;
  padding: 10px;
}
  
.price{
  color: green;
  font-size: 24px;
}

.box_img{
  display: flex;
  flex-direction: row;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  width: 96px;
  height: 96px;
  margin-right: 16px;
 }

.item_box{
  width: 100%;
  display: flex;
  flex-direction: row;
}

.item_box_btn{
  margin-left: auto;
  border: none;
  padding: 10px;
}